import React, { useContext, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { TextField, IconButton, Button, Paper } from "@material-ui/core";
import { UserContext } from "../../config/UserContext";
import DoneIcon from "@material-ui/icons/Done";
import HappyIcon from "@material-ui/icons/SentimentVerySatisfiedRounded";
import BackIcon from "@material-ui/icons/ArrowBackIos";
import PhotoCamera from "@material-ui/icons/PhotoCameraRounded";
import { firestore } from "../../config/firebase";
import configDB from "../../config/firebase";
import Alert from "@material-ui/lab/Alert";

const Register = ({ onPageChange }) => {
  const currentUser = useContext(UserContext);
  const [name, setName] = useState("");
  const [age, setAge] = useState("");
  const [source, setSource] = useState("");
  const [imageUrl, setImageUrl] = useState();
  const [warningMessage, setWarningMessage] = useState("");
  
  const savePost = () => {
    firestore
      .collection("children")
      .add({
        name: name,
        age: age,
        photo:imageUrl
      })
      .then(function (docRef) {
        setWarningMessage("Seu projeto foi cadastrado!");
      })
      .catch(function (error) {
        console.error("Error adding document: ", error);
      });
  };

  const handleCapture = async (target) => {
    if (target.files) {
      if (target.files.length !== 0) {
        const file = target.files[0];
        const storageRef = configDB.storage().ref();
        const fileRef = storageRef.child(file.name);
        await fileRef.put(file).then(() => {
          console.log("Uploaded file", file.name);
        });

        setImageUrl(await fileRef.getDownloadURL());

        const newUrl = URL.createObjectURL(file);
        setSource(newUrl);
      }
    }
  };
  const classes = useStyles();
  return (
    <div className={classes.root}>
      {warningMessage !== "" && (
        <Alert
          className={classes.alert}
          icon={<HappyIcon fontSize="inherit" />}
          variant="filled"
          severity="success"
        >
          {warningMessage}
        </Alert>
      )}

      <div className={classes.div}>
        <TextField
          id="description"
          label="Nome"
          variant="outlined"
          fullWidth
          className={classes.textButton}
          onChange={(e) => setName(e.target.value)}
        />
      </div>
      <div className={classes.div}>
        <TextField
          id="outlined-number"
          label="Idade"
          variant="outlined"
          type="number"
          fullWidth
          className={classes.textButton}
          onChange={(e) => setAge(e.target.value)}
        />
      </div>

      {source !== "" ? (
        <Paper className={classes.imgBox}>
          <img src={source} alt={"snap"} className={classes.img}></img>
        </Paper>
      ) : (
        <div>
          <img
            className={classes.media}
            src={require("../../assets/picture-girl.png")}
            alt="girl"
          />
        </div>
      )}

      <div className={classes.div}>
        <input
          accept="image/*"
          className={classes.input}
          id="icon-button-file"
          type="file"
          capture="environment"
          onChange={(e) => handleCapture(e.target)}
        />

        <label className={classes.label} htmlFor="icon-button-file">
          <Button
            component="span"
            variant="contained"
            className={classes.photoButton}
            fullWidth
            startIcon={<PhotoCamera />}
          >
            Tirar uma foto
          </Button>
        </label>
      </div>
      <div className={classes.div}>
        <div className={classes.buttonDiv}>
          <IconButton aria-label="voltar" onClick={() => onPageChange("")}>
            <BackIcon />
          </IconButton>
        </div>
        <div className={classes.buttonDiv}>
          <IconButton aria-label="delete" onClick={savePost}>
            <DoneIcon className={classes.doneButton} />
          </IconButton>
        </div>
      </div>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    paddingTop: "10px",
  },
  div: {
    display: "flex",
  },
  doneButton: {
    margin: "5px",
    fill: "#04bd0d",
  },
  buttonDiv: {
    flex: 1,
  },
  textButton: {
    margin: "10px",
  },
  alert: {
    margin: "15px 0px 15px 0px",
  },
  imgBox: {
    maxWidth: "100%",
    maxHeight: "80%",
    margin: "10px",
    display: "flex",
    justifyContent: "center",
  },
  img: {
    height: "inherit",
    maxWidth: "inherit",
  },
  media: {
    height: 200,
  },
  photoButton: {
    margin: "5px 5px 10px 5px",
    backgroundColor: "#db826e",
    color: "#ffffff",
    "&:hover": {
      backgroundColor: "#db826e",
      color: "#ffffff",
    },
  },
  label: {
    display: "contents",
  },
  input: {
    display: "none",
  },
}));

export default Register;
