import React, { useContext, useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Button, Paper, Typography } from "@material-ui/core";
import { UserContext } from "../../config/UserContext";

import RegisterProject from "./RegisterProject";
import RegisterChild from "./RegisterChild";

const Register = () => {
  const currentUser = useContext(UserContext);

  const [currentPage, setCurrentPage] = useState("");

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };
  const handleClick = () => {
    console.info("You clicked the Chip.");
  };

  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Paper className={classes.paper}>
        <Typography
          className={classes.typography}
          variant="h6"
          color="textSecondary"
          component="p"
        >
          Cadastro
        </Typography>
        {currentPage === "" ? <div>
          <div className={classes.divImg}>
            <img
              className={classes.media}
              src={require("../../assets/register-girl.png")}
              alt="girl"
            />
          </div>
          <div className={classes.div}>
            <Button
              component="span"
              variant="contained"
              className={classes.button}
              fullWidth
              onClick={() => setCurrentPage("RegisterChild")}
            >
              cadastrar criança
            </Button>
          </div>
          <div className={classes.div}>
            <Button
              component="span"
              variant="contained"
              className={classes.button}
              fullWidth
              onClick={() => setCurrentPage("RegisterProject")}
            >
              cadastrar projeto
            </Button>
          </div>
        </div> : currentPage === "RegisterProject" ? <RegisterProject onPageChange={handlePageChange}></RegisterProject> : <RegisterChild onPageChange={handlePageChange}></RegisterChild>}
      </Paper>
    </div>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: "hidden",
    padding: "6vh 1vh",
    height: "100%",
    verticalAlign: "middle",
    textAlign: "center",
    flexDirection: "column",
  },
  paper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 500,
  },
  divImg: {
    alignItems: "center",
    display: "flex",
    justifyContent: "center",
    marginBottom: "2vh",
  },
  div: {
    display: "flex",
  },
  button: {
    margin: "5px 5px 10px 5px",
    backgroundColor: "#db826e",
    color: "#ffffff",
    "&:hover": {
      backgroundColor: "#db826e",
      color: "#ffffff",
    },
  },
  media: {
    height: 200,
  },
}));

export default Register;
