import React, { useState} from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  Typography,
  Paper,
  Container,
} from "@material-ui/core";
import "cropperjs/dist/cropper.css";
import Camera from "./Camera";
import PhotoConfig from "./PhotoConfig";

const CreatePost = () => {
  const classes = useStyles();

  const [currentPage, setCurrentPage] = useState("Camera");
  const [blob, setBlob] = useState();

  const handlePageChange = (page) => {
    setCurrentPage(page);
  };

  const getBlob = (blob) => {
    setBlob(blob);
  };

  return (
    <Container className={classes.root}>
      <Paper className={classes.mainPaper}>
        <div className={classes.paper}>
          <Typography
            className={classes.typography}
            variant="h6"
            color="textSecondary"
            component="p"
          >
            Nova publicação
          </Typography>
          <div>
            {currentPage === "Camera" ? (
              <Camera
                onPageChange={handlePageChange}
                getBlob={getBlob}
              ></Camera>
            ) : ( currentPage === "PhotoConfig" && 
              <PhotoConfig
                onPageChange={handlePageChange}
                blob={blob}
              ></PhotoConfig>
            )}
          </div>
          {/* <img style={{ width: "100%" }} src={blob} alt="cropped" /> */}
        </div>
      </Paper>
    </Container>
  );
};

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    overflow: "hidden",
    height: "100%",
    textAlign: "center",
    flexDirection: "column",
    marginBottom: "60px",
    padding: "6vh 1vh",
  },
  mainPaper: {
    padding: theme.spacing(2),
    margin: "auto",
    maxWidth: 500,
  },
  imgBox: {
    maxWidth: "100%",
    maxHeight: "80%",
    margin: "10px",
    display: "flex",
    justifyContent: "center",
  },
  div: {
    display: "flex",
  },
  label: {
    display: "contents",
  },
  input: {
    display: "none",
  },
  photoButton: {
    margin: "5px 5px 10px 5px",
    backgroundColor: "#db826e",
    color: "#ffffff",
    "&:hover": {
      backgroundColor: "#db826e",
      color: "#ffffff",
    },
  },
  typography: {
    margin: "15px",
  },
  paper: {
    flex: 1,
    flexDirection: "column",
    padding: "10px 10px 0px 10px",
  },
}));

export default CreatePost;
